.homeContainer {
  width: 800px;
  height: 700px;
  padding: 30px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.homeTitle {
  font-size: 30px;
  font-weight: bold;
  color: #19d157;
}

.LogoutBtn {
  padding: 7px;
  background-color: #19d157;
  border: 0;
  border-radius: 5px;
  font-size: 13px;
  color: white;
  position: absolute;
  top: 30px;
  right: 30px;
}

.LogoutBtn:hover {
  background-color: #00a337;
  cursor: pointer;
}
